.app {
  width: 100%;
  height: 100%;
  background: #fff;
}
.app .app-header {
  height: 55px;
  line-height: 55px;
  background: #c21111;
  color: #fff;
  text-align: center;
}
.app .app-header .app-logo {
  width: 40px;
  height: 32px;
  margin-top: -5px;
  vertical-align: middle;
}
.app .app-header .app-title {
  display: inline-block;
  height: 55px;
  line-height: 55px;
  font-size: 18px;
}
.app .nav-box {
  display: -ms-flexbox;
  display: flex;
  height: 35px;
  line-height: 33px;
}
.app .nav-box .nav-item {
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: center;
}
.app .nav-box .nav-link,
.app .nav-box .link {
  text-decoration: none;
  display: block;
}
.app .nav-box .nav-link.active,
.app .nav-box .link.active {
  color: #c21111;
  border-bottom: 2px solid #c21111;
}
.app .view-box {
  position: fixed;
  top: 90px;
  left: 0;
  right: 0;
  bottom: 52px;
}
.app .translate-enter {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.app .translate-enter.translate-enter-active {
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  -o-transition: transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}



