.music-album {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 99;
  background-color: #fff;
}
.music-album .music-header {
  z-index: 10;
}
.music-album .album-img {
  position: relative;
  z-index: -1;
  width: 100%;
  padding-top: 70%;
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-transform-origin: top center;
      -ms-transform-origin: top center;
          transform-origin: top center;
  -webkit-transition: -webkit-transform;
  transition: -webkit-transform;
  -o-transition: transform;
  transition: transform;
  transition: transform, -webkit-transform;
}
.music-album .album-img.fixed {
  position: fixed;
  top: 0;
  height: 55px;
  z-index: 1;
  background-position: 0 0;
  padding: 0;
  display: none;
}
.music-album .play-wrapper {
  position: absolute;
  bottom: 20px;
  width: 100%;
  text-align: center;
  color: #fff;
}
.music-album .play-wrapper .play-button {
  width: 40%;
  padding: 10px 0;
  margin: 0 auto;
  border: 1px solid #fff;
  border-radius: 18px;
}
.music-album .play-wrapper i {
  display: inline-block;
  vertical-align: middle;
  margin-top: -5px;
  margin-right: 5px;
  font-size: 16px;
  font-weight: bold;
}
.music-album .play-wrapper span {
  font-size: 14px;
}
.music-album .filter {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}
.music-album .album-container {
  position: absolute;
  bottom: 52px;
  width: 100%;
  margin-top: 0;
}
.music-album .album-container .album-scroll {
  width: 100%;
  height: 100%;
}
.music-album .album-container .scroll-view {
  overflow: visible;
}
.music-album .album-container .album-wrapper {
  padding: 25px 25px 0 25px;
  background: #fff;
}
.music-album .album-container .album-wrapper .song-count {
  font-size: 14px;
}
.music-album .album-container .album-wrapper .song-list {
  margin-top: 20px;
}
.music-album .album-container .album-wrapper .song-list .song {
  padding-bottom: 25px;
}
.music-album .album-container .album-wrapper .song-list .song .song-name {
  height: 16px;
  font-size: 14px;
  color: #333;
}
.music-album .album-container .album-wrapper .song-list .song .song-singer {
  font-size: 12px;
  padding-top: 8px;
  color: #333;
}
.music-album .album-container .album-wrapper .song-list .song .song-name,
.music-album .album-container .album-wrapper .song-list .song .song-singer {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.music-album .album-container .album-wrapper .album-info {
  padding-bottom: 25px;
}
.music-album .album-container .album-wrapper .album-info .album-title {
  font-size: 18px;
  font-weight: 100;
  text-align: center;
  color: #333;
}
.music-album .album-container .album-wrapper .album-info .album-desc {
  font-size: 14px;
  margin-top: 15px;
  line-height: 22px;
}



