.music-header {
  position: fixed;
  width: 100%;
  height: 55px;
  line-height: 55px;
  color: #fff;
  text-align: center;
  font-size: 18px;
}
.music-header .header-back {
  position: absolute;
  left: 10px;
  font-size: 24px;
  line-height: 50px;
}
.music-header .header-back .iconfont {
  font-size: 22px;
}
.music-header .header-title {
  margin: 0 40px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}



