.play-list-bg {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1002;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.play-list-bg.fade-enter .play-list-wrap {
  -webkit-transform: translate3d(0, 100%, 0);
          transform: translate3d(0, 100%, 0);
}
.play-list-bg.fade-enter-active .play-list-wrap {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transition: -webkit-transform .5s;
  transition: -webkit-transform .5s;
  -o-transition: transform .5s;
  transition: transform .5s;
  transition: transform .5s, -webkit-transform .5s;
}
.play-list-bg.fade-exit .play-list-wrap {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.play-list-bg.fade-exit-active .play-list-wrap {
  -webkit-transform: translate3d(0, 100%, 0);
          transform: translate3d(0, 100%, 0);
  -webkit-transition: -webkit-transform .5s;
  transition: -webkit-transform .5s;
  -o-transition: transform .5s;
  transition: transform .5s;
  transition: transform .5s, -webkit-transform .5s;
}
.play-list-bg .play-list-wrap {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #fff;
  font-size: 16px;
  color: #333;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.play-list-bg .play-list-wrap .play-list-head {
  position: relative;
  height: 40px;
  line-height: 40px;
}
.play-list-bg .play-list-wrap .play-list-head:after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: #CCCCCC;
  -webkit-transform: scaleY(0.5);
      -ms-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.play-list-bg .play-list-wrap .play-list-head .head-title,
.play-list-bg .play-list-wrap .play-list-head .close {
  font-weight: 400;
}
.play-list-bg .play-list-wrap .play-list-head .head-title {
  float: left;
  margin-left: 12px;
}
.play-list-bg .play-list-wrap .play-list-head .close {
  float: right;
  margin-right: 20px;
}
.play-list-bg .play-list-wrap .play-list {
  height: 250px;
}
.play-list-bg .play-list-wrap .play-list .play-list-item .item-left {
  float: left;
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
}
.play-list-bg .play-list-wrap .play-list .play-list-item .item-right {
  position: relative;
  height: 50px;
  margin-left: 50px;
  padding-top: 2px;
}
.play-list-bg .play-list-wrap .play-list .play-list-item .item-right:after {
  position: absolute;
  content: "";
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: #CCCCCC;
  -webkit-transform: scaleY(0.5);
      -ms-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.play-list-bg .play-list-wrap .play-list .play-list-item .item-right .song {
  padding-top: 5px;
  line-height: 18px;
  margin-right: 50px;
}
.play-list-bg .play-list-wrap .play-list .play-list-item .item-right .song .song-name,
.play-list-bg .play-list-wrap .play-list .play-list-item .item-right .song .song-singer {
  display: block;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 16px;
}
.play-list-bg .play-list-wrap .play-list .play-list-item .item-right .song .song-singer {
  color: rgba(211, 211, 211, 0.7);
  font-size: 14px;
}
.play-list-bg .play-list-wrap .play-list .play-list-item .item-right .song.current .song-name,
.play-list-bg .play-list-wrap .play-list .play-list-item .item-right .song.current .song-singer {
  color: #c21111;
}
.play-list-bg .play-list-wrap .play-list .play-list-item .item-right .delete {
  position: absolute;
  right: 30px;
  top: 16px;
  font-size: 22px;
}
.play-list-bg .play-list-wrap .play-list .play-list-item:last-child .item-right:after {
  height: 0;
}
.fade-enter {
  opacity: 0;
}
.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity .5s;
  -o-transition: opacity .5s;
  transition: opacity .5s;
}
.fade-exit {
  opacity: 1;
}
.fade-exit.fade-exit-active {
  opacity: 0;
  -webkit-transition: opacity .5s;
  -o-transition: opacity .5s;
  transition: opacity .5s;
}



