.recommend-box {
  width: 100%;
  height: 100%;
}
.recommend-box .slider-container {
  height: 160px;
  position: relative;
}
.recommend-box .slider-container .slider-nav {
  display: block;
  width: 100%;
  height: 100%;
}
.recommend-box .slider-container .swiper-pagination-bullet-active {
  background-color: #eeeeee;
}
.recommend-box .album-container .title {
  height: 50px;
  line-height: 50px;
  text-align: center;
  font-size: 15px;
  font-weight: normal;
  color: #c21111;
}
.recommend-box .album-container .album-list {
  font-size: 14px;
  padding-bottom: 20px;
}
.recommend-box .album-container .album-list .album-wrapper {
  margin: 20px;
  color: #333;
}
.recommend-box .album-container .album-list .album-wrapper .left {
  display: inline-block;
  width: 60px;
  height: 60px;
  vertical-align: top;
}
.recommend-box .album-container .album-list .album-wrapper .right {
  display: inline-block;
  max-width: 200px;
  margin-left: 20px;
  line-height: 20px;
}
.recommend-box .album-container .album-list .album-wrapper .right .album-name,
.recommend-box .album-container .album-list .album-wrapper .right .singer-name,
.recommend-box .album-container .album-list .album-wrapper .right .public-time {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.recommend-box .album-container .album-list .album-wrapper .right .album-name {
  color: #333;
  font-weight: bold;
}
.recommend-box .album-container .album-list .album-wrapper .right .singer-name,
.recommend-box .album-container .album-list .album-wrapper .right .public-time {
  font-size: 12px;
  color: #787878;
}
.recommend-box .album-container .album-list .album-wrapper:first-child {
  margin-top: 0;
}
.recommend-box .album-container .album-list .album-wrapper:last-clild {
  margin-bottom: 0;
}



