.music-search {
  height: 100%;
}
.music-search .search-box-wrapper {
  position: relative;
  padding: 20px;
}
.music-search .search-box {
  padding: 10px;
  background-color: #fff;
  border-radius: 3px;
  line-height: 0;
  border: 1px solid #666;
}
.music-search .search-box i {
  font-size: 20px;
  position: relative;
  top: 8px;
}
.music-search .search-box .search-input {
  width: 80%;
  margin-left: 10px;
  background: none;
  border: none;
  outline: none;
  vertical-align: top;
  color: #999;
}
.music-search .cancel-button {
  position: absolute;
  top: 50%;
  right: 30px;
  margin-top: -8px;
  height: 16px;
  line-height: 1;
  font-size: 14px;
  display: none;
}
.music-search .search-hot {
  position: absolute;
  left: 20px;
  right: 20px;
  display: none;
}
.music-search .search-hot .title {
  font-size: 14px;
  color: #333;
}
.music-search .search-hot .hot-list .hot-item {
  float: left;
  padding: 5px 8px;
  margin-right: 10px;
  margin-top: 10px;
  border-radius: 10px;
  font-size: 14px;
  background-color: #efffff;
}
.music-search .search-result {
  position: absolute;
  left: 12px;
  right: 20px;
  top: 70px;
  bottom: 0;
}
.music-search .search-result .album-wrapper {
  margin: 10px 0 0 0px;
}
.music-search .search-result .album-wrapper .left {
  display: inline-block;
  width: 40px;
  height: 40px;
  border: 1px solid #eee;
}
.music-search .search-result .album-wrapper .left img {
  width: 100%;
  height: 100%;
}
.music-search .search-result .album-wrapper .right {
  display: inline-block;
  margin-left: 10px;
  vertical-align: top;
  max-width: 200px;
}
.music-search .search-result .album-wrapper .right .song,
.music-search .search-result .album-wrapper .right .singer {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.music-search .search-result .album-wrapper .right .song {
  margin-top: 5px;
  padding: 1px 0;
  color: #333;
  font-size: 14px;
}
.music-search .search-result .album-wrapper .right .singer {
  margin-top: 5px;
  font-size: 12px;
}
.music-search .search-result .singer-wrapper {
  margin: 10px 0 0 0px;
}
.music-search .search-result .singer-wrapper .left {
  display: inline-block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid #eee;
}
.music-search .search-result .singer-wrapper .left img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.music-search .search-result .singer-wrapper .right {
  display: inline-block;
  margin-left: 10px;
  vertical-align: top;
}
.music-search .search-result .singer-wrapper .right .singer {
  margin-top: 5px;
  color: #333;
  font-size: 14px;
}
.music-search .search-result .singer-wrapper .right .info {
  margin-top: 5px;
  font-size: 12px;
}
.music-search .search-result .song-wrapper {
  margin: 10px 0;
}
.music-search .search-result .song-wrapper .left {
  display: inline-block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 22px;
  border: 1px solid #eee;
}
.music-search .search-result .song-wrapper .left i {
  font-size: 22px;
}
.music-search .search-result .song-wrapper .right {
  display: inline-block;
  margin-left: 10px;
  vertical-align: top;
  max-width: 200px;
}
.music-search .search-result .song-wrapper .right .song,
.music-search .search-result .song-wrapper .right .singer {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.music-search .search-result .song-wrapper .right .song {
  padding: 1px 0;
  color: #333;
  font-size: 14px;
}
.music-search .search-result .song-wrapper .right .singer {
  margin-top: 5px;
  font-size: 12px;
}



