.music-singer {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #212121;
  z-index: 100;
}
.music-singer .music-header {
  z-index: 10;
}
.music-singer .singer-img {
  position: relative;
  z-index: -1;
  width: 100%;
  padding-top: 70%;
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-transform-origin: top center;
      -ms-transform-origin: top center;
          transform-origin: top center;
  -webkit-transition: -webkit-transform;
  transition: -webkit-transform;
  -o-transition: transform;
  transition: transform;
  transition: transform, -webkit-transform;
}
.music-singer .singer-img.fixed {
  position: fixed;
  top: 0;
  height: 55px;
  z-index: 1;
  background-position: 0 0;
  padding: 0;
  display: none;
}
.music-singer .play-wrapper {
  position: absolute;
  bottom: 20px;
  width: 100%;
  text-align: center;
  color: #fff;
}
.music-singer .play-wrapper .play-button {
  display: inline-block;
  width: 120px;
  padding: 8px 0;
  border-radius: 50px;
  border: 1px solid #fff;
}
.music-singer .play-wrapper .play-button i {
  display: inline-block;
  vertical-align: middle;
  margin-top: -2px;
  margin-right: 5px;
  font-size: 16px;
  font-weight: 600;
}
.music-singer .play-wrapper .play-button span {
  font-size: 14px;
}
.music-singer .filter {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}
.music-singer .singer-container {
  position: absolute;
  bottom: 52px;
  width: 100%;
  margin-top: 2px;
}
.music-singer .singer-container .singer-scroll {
  width: 100%;
  height: 100%;
}
.music-singer .singer-container .scroll-view {
  overflow: visible;
}
.music-singer .singer-container .singer-wrapper {
  padding: 25px 25px 0 25px;
  background-color: #fff;
}
.music-singer .singer-container .singer-wrapper .song-count {
  font-size: 14px;
}
.music-singer .singer-container .singer-wrapper .song-list {
  margin-top: 20px;
}
.music-singer .singer-container .singer-wrapper .song-list .song {
  padding-bottom: 25px;
}
.music-singer .singer-container .singer-wrapper .song-list .song .song-name {
  height: 16px;
  color: #333;
  font-size: 14px;
}
.music-singer .singer-container .singer-wrapper .song-list .song .song-singer {
  font-size: 12px;
  padding-top: 8px;
  color: #333;
}
.music-singer .singer-container .singer-wrapper .song-list .song .song-name,
.music-singer .singer-container .singer-wrapper .song-list .song .song-singer {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}



