.mini-player {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 1000;
  width: 100%;
  height: 52px;
  background-color: #c21111;
  color: #FFFFFF;
}
.mini-player .player-img {
  position: absolute;
  width: 46px;
  height: 46px;
  left: 10px;
  top: -6px;
  border: 2px solid rgba(221, 221, 221, 0.3);
  border-radius: 50%;
  background: #fff;
}
.mini-player .player-img.rotate {
  -webkit-animation: rotate 15s linear infinite;
          animation: rotate 15s linear infinite;
}
.mini-player .player-img img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.mini-player .player-center {
  height: 52px;
  padding: 6px 15px 0 66px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.mini-player .player-center span {
  display: block;
}
.mini-player .player-center .song,
.mini-player .player-center .singer {
  max-width: 220px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.mini-player .player-center .song {
  padding-top: 5px;
  font-size: 14px;
}
.mini-player .player-center .singer {
  margin-top: 2px;
  font-size: 12px;
  color: #c21111;
}
.mini-player .player-center .progress-wrapper .progress-bar {
  height: 2px;
}
.mini-player .player-right {
  position: absolute;
  top: 15px;
  right: 15px;
  padding: 1px;
  color: #fff;
  vertical-align: middle;
  line-height: 26px;
}
.mini-player .player-right i {
  font-size: 26px;
}
.mini-player .player-right i.icon-shangyishou1,
.mini-player .player-right i.icon-zanting {
  position: relative;
  top: 2px;
}
.mini-player .player-right .ml-10 {
  margin-left: 10px;
}
.mini-player .filter {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1;
}
.mini-player .filter:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  margin: -30px;
}



