.loading-container {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 100;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.loading-container .loading-wrapper {
  display: inline-block;
  font-size: 12px;
  text-align: center;
}
.loading-container .loading-wrapper .loading-title {
  margin-top: 5px;
}



