.ranking-info {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  background: #fff;
}
.ranking-info .music-header {
  z-index: 10;
}
.ranking-info .ranking-img {
  position: relative;
  z-index: -1;
  width: 100%;
  padding-top: 70%;
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-transform-origin: top center;
      -ms-transform-origin: top center;
          transform-origin: top center;
  -webkit-transition: -webkit-transform;
  transition: -webkit-transform;
  -o-transition: transform;
  transition: transform;
  transition: transform, -webkit-transform;
}
.ranking-info .ranking-img.fixed {
  position: fixed;
  top: 0;
  height: 55px;
  z-index: 1;
  background-position: 0 0;
  padding: 0;
  display: none;
}
.ranking-info .play-wrapper {
  position: absolute;
  bottom: 20px;
  width: 100%;
  text-align: center;
  color: #fff;
}
.ranking-info .play-wrapper .play-button {
  display: inline-block;
  width: 120px;
  padding: 8px 0;
  border-radius: 50px;
  border: 1px solid #fff;
}
.ranking-info .play-wrapper .play-button i {
  display: inline-block;
  vertical-align: middle;
  margin-top: -2px;
  margin-right: 5px;
  font-size: 16px;
  font-weight: 600;
}
.ranking-info .play-wrapper .play-button span {
  font-size: 14px;
}
.ranking-info .filter {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}
.ranking-info .ranking-container {
  position: absolute;
  bottom: 52px;
  width: 100%;
  margin-top: 2px;
}
.ranking-info .ranking-container .ranking-scroll {
  width: 100%;
  height: 100%;
}
.ranking-info .ranking-container .scroll-view {
  overflow: visible;
}
.ranking-info .ranking-container .ranking-wrapper {
  padding: 25px 25px 0 25px;
  background-color: #fff;
}
.ranking-info .ranking-container .ranking-wrapper .ranking-count {
  font-size: 14px;
}
.ranking-info .ranking-container .ranking-wrapper .song-list {
  margin-top: 20px;
}
.ranking-info .ranking-container .ranking-wrapper .song-list .song {
  padding-bottom: 25px;
}
.ranking-info .ranking-container .ranking-wrapper .song-list .song .song-index {
  float: left;
  height: 35px;
  width: 45px;
  line-height: 35px;
  margin-left: -18px;
  text-align: center;
}
.ranking-info .ranking-container .ranking-wrapper .song-list .song:nth-child(1) {
  color: #ffa500;
}
.ranking-info .ranking-container .ranking-wrapper .song-list .song:nth-child(2) {
  color: #ffd700;
}
.ranking-info .ranking-container .ranking-wrapper .song-list .song:nth-child(3) {
  color: #ffec8b;
}
.ranking-info .ranking-container .ranking-wrapper .song-list .song .song-name {
  font-size: 14px;
  height: 16px;
  color: #333;
}
.ranking-info .ranking-container .ranking-wrapper .song-list .song .song-singer {
  font-size: 12px;
  padding-top: 8px;
  color: #333;
}
.ranking-info .ranking-container .ranking-wrapper .song-list .song .song-name,
.ranking-info .ranking-container .ranking-wrapper .song-list .song .song-singer {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.ranking-info .ranking-container .ranking-wrapper .song-list .info {
  padding-bottom: 25px;
}
.ranking-info .ranking-container .ranking-wrapper .song-list .info .ranking-title {
  font-size: 18px;
  font-weight: 100;
  text-align: center;
  color: #ffffff;
}
.ranking-info .ranking-container .ranking-wrapper .song-list .info .ranking-desc {
  font-size: 14px;
  margin-top: 15px;
  line-height: 22px;
}



