.music-ranking {
  width: 100%;
  height: 100%;
}
.music-ranking .ranking-list {
  padding-bottom: 15px;
}
.music-ranking .ranking-list .ranking-wrapper {
  margin: 15px;
  background-color: #fff;
  -webkit-box-shadow: 0 0 5px 1px #eee;
          box-shadow: 0 0 5px 1px #eee;
  font-size: 0;
}
.music-ranking .ranking-list .ranking-wrapper .left {
  display: inline-block;
  width: 100px;
  height: 100px;
}
.music-ranking .ranking-list .ranking-wrapper .left img {
  width: 100px;
  height: 100px;
}
.music-ranking .ranking-list .ranking-wrapper .right {
  display: inline-block;
  line-height: 22px;
  font-size: 14px;
  margin-left: 10px;
  vertical-align: top;
}
.music-ranking .ranking-list .ranking-wrapper .right .ranking-title {
  font-size: 16px;
  color: #333;
  margin: 4px 0 6px 0;
}
.music-ranking .ranking-list .ranking-wrapper .right .top-song,
.music-ranking .ranking-list .ranking-wrapper .right .ranking-title {
  max-width: 180px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.music-ranking .ranking-list .ranking-wrapper .right .top-song .index,
.music-ranking .ranking-list .ranking-wrapper .right .ranking-title .index {
  margin-right: 10px;
}
.music-ranking .ranking-list .ranking-wrapper .right .top-song .song,
.music-ranking .ranking-list .ranking-wrapper .right .ranking-title .song {
  color: #999;
}



